        <div class="w1200 c-b mm100 ovh foot2">
          <p class="fs12 corw">Copyright  2010-2020 XXXXXXXXX  XXXXXX.com  400-888-8888</p>
          <p class="fs12 corw mgt5"> 1101010200111111  | ICP111111 |  SP1101051111111111-1 |  | </p>
