<template>
  <!---->
  <div class=" pdt30 pdb30 pdr15 set pdl20 pdr20">
    <div class="">
      <p class="fs18 h28 lh28 cor0"></p>
    </div>

    <div class="fl mal20 mgl20" style="margin-left: 0px;margin-top: 10px;">
      <select class="form-control ios0 cursor" v-model="map.subjectId" @change="selectSub">
        <option value="0"></option>
        <option  :value="item.subjectId" v-for="(item,index) in subjectList">{{item.subName}}</option>
      </select>
    </div>


    <div class="mgt30">
      <ul class="ul0004">
        <!--1-->
        <li class="db">
          <div class="ovh mgt20">
            
          </div>
          <div class="mgt30">
            <table class="tab2">
              <tbody>
              <tr>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
              </tr>
              <tr v-for="(item,index) in budgetUseList">
                <td>{{item.budgetYear}}</td>
                <td>{{item.effectiveMonth}}</td>
                <td>{{item.orgName}}</td>
                <td>{{item.subName}}</td>
                <td>{{item.budgetAmount}}</td>
                <td>{{item.actualUse}}</td>
                <td>{{item.budgetAmount-item.actualUse}}</td>
                <td>
                  <router-link class="btn btn-xs btn-primary" :to="{name:'budgetUseDetail',query:{budgetYear:item.budgetYear,subjectId:item.subjectId,orgId:item.orgId}}"></router-link>
                  <template v-if="item.planId!=null">
                    <router-link class="btn btn-xs btn-success" :to="{name:'budgetUseSchedule',query:{planId:item.planId,amount:item.budgetAmount,budgetYear:item.budgetYear,subjectId:item.subjectId,orgId:item.orgId}}"></router-link>
                  </template>
                </td>

              </tr>
              </tbody>
            </table>
          </div>
        </li>

      </ul>
    </div>
    <!---->
    <div class="fen c-b ovh mgt30 t-c w410">
      <page-vue :pageData="pageData" @chekdpageCallcak="chekdpage"></page-vue>
    </div>
    <!--END-->


  </div>
  <!--END-->
</template>

<script>
  var _this = '';
  export default {
    name: "budgetQuery",
    data(){
      return{
        map:{
          pageNum:1,
          pageSize:7,
          currOrgId:32,
          subjectId:0,
          searchKey:null
        },
        budgetUseList:[],//
        subjectList:[],
        pageData:{
          curPage:1,//
          totalCount:0,//
          pageSize:7//
        },

      }
    },
    beforeCreate(){
      _this = this;
    },
    created(){
      //
      this.configJS.getUserInfo(this);
      _this.map.currOrgId=this.configJS.userInfo.orgId;
      initSubjectList();//
      initBudgetUseList();
    },
    methods:{
      chekdpage: function(pageNum) {//
        _this.map.pageNum = pageNum;
        _this.pageData.curPage = pageNum;
          initBudgetUseList();

      },
      selectSub:function () {//
        initBudgetUseList();
      }

    },
  }


  function initBudgetUseList() {//
    _this.$axios({
      method: 'get',
      url: '/biz/tob-budget/biz/budget/budgetUseList',
      params:  _this.map
    }).then((res) => {
      if(res.data.code==200){
        _this.budgetUseList=res.data.obj.rows;
        _this.pageData.totalCount=res.data.obj.total;//
        _this.pageData.curPage= _this.map.pageNum;//
        _this.pageData.pageSize= _this.map.pageSize;//
      }
    }).catch((err) => {
      console.log(err);
    })
  }
  function initSubjectList() {//
    _this.$axios({
      method: 'get',
      url: '/biz/tob-budget/biz/budget/findSubjectList',
      params:{currOrgId:_this.map.currOrgId}
    }).then((res) => {
      if(res.data.code==200){
        console.log(res)
        _this.subjectList= res.data.obj;
      }
    }).catch((err) => {
      console.log(err);
    })
  }
</script>

<style scoped>

</style>



