<template>
  <div class="wrap1 ww100 pdt30 pdl20 pdr20 pdb30">

    <div>
      <p class="fs18 h28 lh28 cor0"></p>
      <ul class="ul003 mgt20">
        <li class="active" @click.prevent="checkTab = 1"><a href="###"></a></li>
        <li id="allocation" @click.prevent="checkTab = 2"><a href="###"></a></li>
        <li id="use" @click.prevent="checkTab = 3"><a href="###"></a></li>
      </ul>

      <div class="mgt30">
        <ul class="ul0004">
          <!--1-->
          <li class="db">
            <div class="ovh mgt20">
              <div class="fl mgr20">
                <select class="form-control ios0" v-model="map.budgetYear" @change="selectYear">
                  <option value="0"></option>
                  <option  :value="item" v-for="(item,index) in yearList">{{item}}</option>
                </select>
              </div>
              <div class="fl">
                <select class="form-control ios0" v-model="map.subjectId" @change="selectSub">
                  <option value="0"></option>
                  <option  :value="item.subjectId" v-for="(item,index) in subjectList">{{item.subName}}</option>
                </select>
             </div>

            </div>

            <div class="mgt30">
              <table class="tab2">
                <tbody>
                <tr>
                  <td></td>
                  <td></td>
                  <td></td>
                  <td></td>
                  <td></td>
                  <td></td>
                </tr>
                <tr v-for="(item,index) in budgetReceiveList">
                  <td>{{item.budgetYear}}</td>
                  <td>{{item.effectiveMonth}}</td>
                  <td>{{item.orgName}}</td>
                  <td>{{item.subName}}</td>
                  <td>{{item.budgetAmount}}</td>
                  <td>
                    <router-link class="btn btn-xs btn-info" :to="{name:'deliveryPlanDetail',query:{'planId':item.planId,'type':item.type}}" v-if="item.planId"></router-link>
                    <span v-else></span>
                    <!--<router-link href="###" class="btn btn-sm btn-primary" :to="{name:'goodsDetail',params:{goodsId:item.goodsId}}"></router-link>-->
                  </td>
                </tr>
                </tbody>
              </table>
            </div>
          </li>
          <!--2-->
          <li class="dn">
            <div class="ovh mgt20">
              <div class="fl mgr20">
                <select class="form-control ios0" v-model="map.budgetYear" @change="selectYear">
                  <option value="0"></option>
                  <option  :value="item" v-for="(item,index) in yearList">{{item}}</option>
                </select>
              </div>
              <div class="fl">
                <select class="form-control ios0" v-model="map.orgId" @change="selectOrg1">
                  <option value="0"></option>
                  <option  :value="item.id" v-for="(item,index) in orgList1">{{item.orgName}}</option>
                </select>
              </div>
              <div class="fr">
                <div class="form-inline" @keyup.13="search">
                  <div class="input-group">
                    <input type="text" class="form-control" placeholder="" v-model="map.searchKey">
                    <span class="input-group-btn">
									<button type="submit" class="btn bged145b" @click="search">
										<i class="glyphicon glyphicon-search corw"></i>
									</button>
								</span>
                  </div>
                </div>
              </div>
            </div>
            <ul class="ulDiv ovh mgt30">
              <li class="fl mgr30">
                <template v-if="checkBottomOrg===true"><!---->
                  <a  class="db pdl29 corED145B ios8 lii"></a>
                  <div class="abs liDiv t-c bor-d6 z1000 bgw dn time05 lii lis">
                    <router-link class="db w100 h40 lh40 bor-b-d6 cor9 deng" :to="{name: 'addBudgetBySub',params:{myTab:2}}"></router-link>
                    <router-link class="db w100 h40 lh40 bor-b-d6 cor9 deng" :to="{name: 'addBudgetByOrgMonth',params:{myTab:2}}"></router-link>
                  </div>
                </template>
                <template v-else><!---->
                  <a  class="db pdl29 corED145B ios8 lii"></a>
                  <div class="abs liDiv t-c bor-d6 z1000 bgw dn time05 lii lis">
                    <router-link class="db w100 h40 lh40 bor-b-d6 cor9 deng" :to="{name: 'addBudgetByOrg',params:{myTab:2}}"></router-link>
                    <router-link class="db w100 h40 lh40 bor-b-d6 cor9 deng" :to="{name: 'addBudgetByOrgMonth',params:{myTab:2}}"></router-link>
                  </div>
                </template>
              </li>
              <li class="fl mgr30"><a href="###" class="db pdl29 corED145B ios4"></a></li>
              <li class="fl mgr30"><a href="###" class="db pdl29 corED145B ios5"></a></li>
              <!--<li class="fl mgr30"><a href="###" class="db pdl29 corED145B ios7"></a></li>-->
            </ul>

            <div class="mgt40">
              <table class="tab2">
                <tbody>
                <tr>
                  <td></td>
                  <td></td>
                  <td></td>
                  <td></td>
                  <td></td>
                  <td></td>
                </tr>
                <tr v-for="(item,index) in budgetAllocationList">
                  <td>{{item.budgetYear}}</td>
                  <td>{{item.effectiveMonth}}</td>
                  <td>{{item.orgName}}</td>
                  <td>{{item.subName}}</td>
                  <td>{{item.budgetAmount}}</td>
                  <td>
                    <router-link class="btn btn-sm btn-primary" :to="{name:'budgetExecute',query:{
                    'year':item.budgetYear,
                    'orgId':item.orgId,
                    'subjectId':item.subjectId,
                    'myTab':2
                    }}"></router-link>
                   <!--  &lt;!&ndash;<router-link href="###" class="btn btn-sm btn-primary" :to="{name:'goodsDetail',params:{goodsId:item.goodsId}}"></router-link>&ndash;&gt; -->
                  </td>
                </tr>
                </tbody>
              </table>
            </div>
          </li>
          <!--3-->
          <li class="dn">
            <div class="ovh mgt20">
              <div class="fl mgr20">
                <select class="form-control ios0" v-model="map.budgetYear" @change="selectYear">
                  <option value="0"></option>
                  <option  :value="item" v-for="(item,index) in yearList">{{item}}</option>
                </select>
              </div>
              <div class="fl">
                <select class="form-control ios0" v-model="map.orgId" @change="selectOrg2">
                  <option value="0"></option>
                  <option  :value="item.id" v-for="(item,index) in orgList2">{{item.orgName}}</option>
                </select>
              </div>
              <div class="fr">
                <div class="form-inline" @keyup.13="search">
                  <div class="input-group">
                    <input type="text" class="form-control" placeholder="" v-model="map.searchKey">
                    <span class="input-group-btn">
									<button type="submit" class="btn bged145b" @click="search">
										<i class="glyphicon glyphicon-search corw"></i>
									</button>
								</span>
                  </div>
                </div>
              </div>
            </div>

            <div class="mgt40">
              <table class="tab2">
                <tbody>
                <tr>
                  <td></td>
                  <td></td>
                  <td></td>
                  <td></td>
                  <td></td>
                  <td></td>
                  <td></td>
                  <td></td>
                  <td></td>
                </tr>

                <tr v-for="(item,index) in budgetUseList">
                  <td>{{item.budgetYear?item.budgetYear:'-'}}</td>
                  <td>{{item.effectiveMonth?item.effectiveMonth +'' :'-'}}</td>
                  <td>{{item.orgName?item.orgName:'-'}}</td>
                  <td>{{item.subName?item.subName:'-'}}</td>
                  <td>{{item.budgetAmount?item.budgetAmount:'-'}}</td>
                  <td>{{item.actualUse?item.actualUse:'0'}}</td>
                  <td>{{
                        item.budgetAmount&&item.actualUse? item.budgetAmount-item.actualUse:item.budgetAmount
                  }}</td>
                  <td>{{
                    item.userType == 0 ? "" :
                    item.userType == 1 ? "" : ""
                  }}</td>
                  <td style="width: 180px;">
                    <router-link  class="btn btn-xs btn-primary" :to="{name:'budgetUseDetail',query:{budgetYear:item.budgetYear,subjectId:item.subjectId,orgId:item.orgId,myTab:3}}"></router-link>
                    <router-link v-show="item.planId!=null" class="btn btn-xs btn-success" :to="{name:'budgetUseSchedule',query:{planId:item.planId,amount:item.budgetAmount,
                    budgetYear:item.budgetYear,subjectId:item.subjectId,orgId:item.orgId,myTab:3}}"></router-link>
                  </td>

                </tr>
                </tbody>
              </table>
            </div>
          </li>

        </ul>
      </div>
      <!---->
      <div class="fen c-b ovh mgt30 t-c w410">
        <page-vue :pageData="pageData" @chekdpageCallcak="chekdpage"></page-vue>
      </div>
      <!--END-->
    </div>







  </div>
</template>

<script>
  import $ from 'jquery'
  var _this = '';
  export default {
    name: "budgetManage",
    data(){
      return{
        map:{
          pageNum:1,
          pageSize:10,
          budgetYear:0,
          currOrgId:1,
          subjectId:0,
          orgId:0,
          searchKey:null,
          type:0
        },
        checkTab:1,//tab
        budgetReceiveList:[],//
        budgetAllocationList:[],//
        budgetUseList:[],//
        yearList:[],
        subjectList:[],
        orgList1:[],//
        orgList2:[],//
        checkBottomOrg:true,//truefalse
        isAdminBud:true,//truefalse
        pageData:{
          curPage:1,//
          totalCount:1,//

          pageSize:10//
        }
      }
    },
    beforeCreate(){
      _this = this;
    },
    created(){
      //
      this.configJS.getUserInfo(this);
      _this.isAdminBud=this.configJS.isAdminBud();
      _this.map.currOrgId=this.configJS.userInfo.orgId;
      initYearList();//
      initSubjectList();//
      initOrgList1();//
      initOrgList2();//
      checkBottomOrg();//,true:
      initBudgetReceiveList();//

    },
    mounted(){
      var $ul003= $('.ul003>li');
      $ul003.click(function(){
        $(this).addClass('active').siblings().removeClass('active');
        var i= $ul003.index(this);
        $('.ul0004>li').eq(i).show().siblings('.ul0004>li').hide();
      });
      if (this.$route.params.myTab==2) {
        $("#allocation").click();
      };
      if (this.$route.params.myTab==3) {
        $("#use").click();
      }

      var lii= $('.lii');
      lii.mouseover(function(){
        $(".lis").show()
      });
      lii.mouseout(function(){
        $(".lis").hide()
      });
    },
    methods:{
      chekdpage: function(pageNum) {//
        _this.map.pageNum = pageNum;
        _this.pageData.curPage = pageNum;
        if(_this.checkTab==1){
           initBudgetReceiveList();
        }else if (_this.checkTab==2){
          initBudgetAllocationList();
        }else{
          initBudgetUseList();
        }



      },
      search:function(){//
        if(_this.checkTab==2){
          initBudgetAllocationList();
        }else if (_this.checkTab==3){
          initBudgetUseList();
        }
      },
      selectYear:function () {//
        if(_this.checkTab==1){
          initBudgetReceiveList();
        }else if (_this.checkTab==2){
          initBudgetAllocationList();
        }else {
            initBudgetUseList();
        }

      },
      selectSub:function () {//
        initBudgetReceiveList();
      },
      selectOrg1:function(){
        initBudgetAllocationList();
      },
      selectOrg2:function () {
        initBudgetUseList();
      }


    },
    watch:{
      checkTab(newVal,oldVal){

        if(newVal != oldVal) {
          _this.map.pageNum=1;
          _this.map.budgetYear=0;
          _this.map.subjectId=0;
          _this.map.orgId=0;
          _this.map.searchKey=null;
          if(_this.checkTab==1){//
            initBudgetReceiveList();
          }else if (_this.checkTab==2) {//
            initBudgetAllocationList();
          }else if (_this.checkTab==3){//
            initBudgetUseList();
          }

        }
      },

    }
  }

  function initBudgetReceiveList() {//
    _this.$axios({
      method: 'get',
      url: '/biz/tob-budget/biz/budget/budgetReceiveList',
      params:  _this.map
    }).then((res) => {
      if(res.data.code==200){
        _this.budgetReceiveList=res.data.obj.rows;
        _this.pageData.totalCount=res.data.obj.total;//
        _this.pageData.curPage= _this.map.pageNum;//
        _this.pageData.pageSize= _this.map.pageSize;//
      }
    }).catch((err) => {
      console.log(err);
    })
  }

  function initBudgetAllocationList() {//

    _this.$axios({
      method: 'get',
      url: '/biz/tob-budget/biz/budget/budgetAllocationList',
      params:  _this.map
    }).then((res) => {
      if(res.data.code==200){
        _this.budgetAllocationList=res.data.obj.rows;
        _this.pageData.totalCount=res.data.obj.total;//
        _this.pageData.curPage= _this.map.pageNum;//
        _this.pageData.pageSize= _this.map.pageSize;//
      }
    }).catch((err) => {
      console.log(err);
    })
  }

  function initBudgetUseList() {//
    _this.$axios({
      method: 'get',
      url: '/biz/tob-budget/biz/budget/budgetUseList',
      params:  _this.map
    }).then((res) => {
      if(res.data.code==200){

        _this.budgetUseList=res.data.obj.rows;
        _this.pageData.totalCount=res.data.obj.total;//
        _this.pageData.curPage= _this.map.pageNum;//
        _this.pageData.pageSize= _this.map.pageSize;//
      }
    }).catch((err) => {
      console.log(err);
    })
  }

  function initYearList() {//
    _this.$axios({
      method: 'get',
      url: '/biz/tob-budget/biz/budget/findYearList'
    }).then((res) => {
      if(res.data.code==200){
       _this.yearList= res.data.obj;
      }
    }).catch((err) => {
      console.log(err);
    })
  }

  function initSubjectList() {//
    _this.$axios({
      method: 'get',
      url: '/biz/tob-budget/biz/budget/findSubjectList',
      params:{currOrgId:_this.map.currOrgId}
    }).then((res) => {
      if(res.data.code==200){
        _this.subjectList= res.data.obj;
      }
    }).catch((err) => {
      console.log(err);
    })
  }

  function checkBottomOrg() {//
    _this.$axios({
      method: 'get',
      url: '/biz/tob-user/system/orgInfo/checkBottomOrg',
      async:false,
      params:{id:_this.map.currOrgId}
    }).then((res) => {
      if(res.data.code==200){
        if(res.data.obj.isBottomOrg==false){
          _this.map.type=1;//
        }else{
          _this.map.type=0;//
        }
          _this.checkBottomOrg=res.data.obj.isBottomOrg;
      }
    }).catch((err) => {
      console.log(err);
    })
  }

  function initOrgList1() {//
    _this.$axios({
      method: 'get',
      url: '/biz/tob-user/system/orgInfo/getOrgSubList',
      params:{orgId:_this.map.currOrgId}
    }).then((res) => {
      if(res.data.code==200){
        _this.orgList1=res.data.obj.getOrgSubList;
      }
    }).catch((err) => {
      console.log(err);
    })
  }


  function initOrgList2() {//
    _this.$axios({
      method: 'get',
      url: '/biz/tob-user/system/orgInfo/findBottomOrgList',
      params:{orgId:_this.map.currOrgId}
    }).then((res) => {
      if(res.data.code==200){
        _this.orgList2=res.data.obj.bottomOrgList;
      }
    }).catch((err) => {
      console.log(err);
    })
  }


  function checkAllocation() {
    _this.$axios({
      method: 'get',
      url: '/biz/tob-budget/biz/budget/checkAllocation',
      params:{orgId:_this.map.currOrgId}
    }).then((res) => {
      if(res.data.code==200 && res.data.obj !=0){
        _this.$alert(",,!");
      }else{
         sessionStorage.setItem('myTab',2);
        _this.$router.push({name: 'addBudgetBySub'});
      }
    }).catch((err) => {
      console.log(err);
    })
  }


</script>

<style scoped>
.demo-table-expand{
  color: red;
}
.cell{
  color: red;
}
.test{
  color: red;
}
</style>



